"use client";

import { Suspense, useEffect, useState } from "react";
import { useSearchParams } from "next/navigation";
import Link from "next/link";
import {
  CheckCircle,
  CalendarDays,
  User,
  Phone,
  Mail,
  Download,
  Printer,
  Home,
  QrCode,
  Tag,
} from "lucide-react";
import { formatPrice, formatDate } from "@/lib/utils";
import { apiGet } from "@/lib/api-client";
import type { BookingConfirmation } from "@/lib/types";

// Add print styles
const printStyles = `
  @media print {
    * { margin: 0 !important; padding: 0 !important; }
    body > * { display: none !important; }
    .ticket-container { display: block !important; }
    .no-print { display: none !important; }
  }
`;

function ConfirmationContent() {
  const searchParams = useSearchParams();
  const reference = searchParams.get("ref");
  const [booking, setBooking] = useState<any>(null);
  const [loading, setLoading] = useState(true);
  const [qrCodeUrl, setQrCodeUrl] = useState<string>("");

  useEffect(() => {
    if (!reference) return;

    const fetchBooking = async () => {
      try {
        const response = await apiGet(`/api/bookings/${reference}`);
        if (response.success) {
          const bookingData = response.data as any;
          setBooking(bookingData);
          // Generate QR code with just the booking reference (simpler QR code)
          const qrUrl = `https://api.qrserver.com/v1/create-qr-code/?size=280x280&data=${bookingData.bookingReference}`;
          setQrCodeUrl(qrUrl);
        }
      } catch (error) {
        console.error("Error fetching booking:", error);
      } finally {
        setLoading(false);
      }
    };

    fetchBooking();
  }, [reference]);

  if (loading) {
    return (
      <section className="flex min-h-[60vh] items-center justify-center py-16">
        <div className="flex flex-col items-center gap-4 text-center">
          <div className="h-12 w-12 animate-spin rounded-full border-4 border-primary border-t-transparent"></div>
          <p className="text-muted-foreground">Loading your booking...</p>
        </div>
      </section>
    );
  }

  if (!booking) {
    return (
      <section className="flex min-h-[60vh] items-center justify-center py-16">
        <div className="flex flex-col items-center gap-4 text-center">
          <h1 className="text-2xl font-bold text-foreground">
            No Booking Found
          </h1>
          <p className="text-muted-foreground">
            We could not find your booking details. Please try booking again.
          </p>
          <Link
            href="/booking"
            className="inline-flex items-center gap-2 rounded-lg bg-primary px-6 py-3 text-sm font-semibold text-primary-foreground"
          >
            Book Tickets
          </Link>
        </div>
      </section>
    );
  }

  const handlePrint = () => {
    // Open a new window with the ticket
    const printWindow = window.open("", "PRINT", "height=800,width=1000");
    if (!printWindow) {
      alert("Please allow pop-ups to print the ticket");
      return;
    }

    const ticketHTML = `
      <!DOCTYPE html>
      <html>
      <head>
        <meta charset="UTF-8">
        <title>Aerocity Booking - ${booking.bookingReference}</title>
        <style>
          * { margin: 0; padding: 0; box-sizing: border-box; }
          body { 
            font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', sans-serif; 
            padding: 20px; 
            background: white;
          }
          .ticket { 
            max-width: 700px; 
            margin: 20px auto; 
            border: 3px solid #0284c7; 
            border-radius: 12px; 
            padding: 40px; 
            background: white;
            box-shadow: 0 10px 30px rgba(0,0,0,0.1);
          }
          .header { text-align: center; margin-bottom: 30px; }
          .header h1 { font-size: 32px; color: #333; margin-bottom: 5px; font-weight: bold; }
          .header p { color: #666; font-size: 14px; }
          .reference { 
            background: linear-gradient(135deg, #0284c7 0%, #0369a1 100%); 
            border-radius: 8px; 
            padding: 25px; 
            text-align: center; 
            margin: 25px 0;
            color: white;
          }
          .reference-label { font-size: 11px; font-weight: bold; opacity: 0.9; }
          .reference-code { 
            font-size: 26px; 
            font-weight: bold; 
            font-family: 'Courier New', monospace; 
            letter-spacing: 3px;
            margin-top: 8px;
          }
          .qr-section { text-align: center; margin: 35px 0; }
          .qr-section img { max-width: 200px; height: auto; border: 2px solid #ddd; border-radius: 8px; }
          .qr-section p { margin-top: 12px; font-size: 12px; color: #666; }
          .details { 
            display: grid; 
            grid-template-columns: 1fr 1fr; 
            gap: 25px; 
            margin: 30px 0;
            padding: 20px 0;
            border-top: 1px solid #eee;
            border-bottom: 1px solid #eee;
          }
          .detail-group { }
          .detail-label { 
            font-size: 11px; 
            color: #666; 
            font-weight: 700; 
            text-transform: uppercase; 
            letter-spacing: 0.5px;
          }
          .detail-value { 
            font-size: 16px; 
            color: #333; 
            margin-top: 6px;
            font-weight: 500;
          }
          .tickets-section { margin: 30px 0; }
          .tickets-section h3 { 
            font-size: 16px; 
            font-weight: bold; 
            margin-bottom: 15px;
            color: #333;
          }
          .ticket-item { 
            display: flex; 
            justify-content: space-between; 
            align-items: center;
            padding: 12px 0; 
            font-size: 14px;
            border-bottom: 1px solid #f0f0f0;
          }
          .ticket-left { flex: 1; }
          .ticket-name { color: #333; font-weight: 500; }
          .ticket-qty { color: #999; font-size: 13px; }
          .ticket-price { color: #666; font-weight: 500; min-width: 80px; text-align: right; }
          .total { 
            display: flex; 
            justify-content: space-between;
            align-items: center;
            border-top: 2px solid #333; 
            padding-top: 15px; 
            margin-top: 15px; 
            font-weight: bold; 
            font-size: 20px;
            color: #0284c7;
          }
          .footer { 
            text-align: center; 
            margin-top: 40px; 
            font-size: 11px; 
            color: #999; 
            padding-top: 20px; 
            border-top: 1px solid #eee;
          }
          @media print { 
            body { padding: 0; margin: 0; background: white; }
            .ticket { 
              margin: 0; 
              box-shadow: none;
              page-break-inside: avoid;
            }
          }
        </style>
      </head>
      <body>
        <div class="ticket">
          <div class="header">
            <h1>AEROCITY</h1>
            <p>Water Park Booking Ticket</p>
          </div>
          
          <div class="reference">
            <div class="reference-label">Booking Reference</div>
            <div class="reference-code">${booking.bookingReference}</div>
          </div>
          
          <div class="qr-section">
            <img src="${qrCodeUrl}" alt="Booking QR Code" />
            <p>Scan this code at gate for entry</p>
          </div>
          
          <div class="details">
            <div class="detail-group">
              <div class="detail-label">Visit Date</div>
              <div class="detail-value">${new Date(booking.visitDate).toLocaleDateString("en-IN", { weekday: "short", month: "short", day: "numeric", year: "numeric" })}</div>
            </div>
            <div class="detail-group">
              <div class="detail-label">Status</div>
              <div class="detail-value" style="color: green;">✓ Confirmed</div>
            </div>
            <div class="detail-group">
              <div class="detail-label">Customer Name</div>
              <div class="detail-value">${booking.customerName}</div>
            </div>
            <div class="detail-group">
              <div class="detail-label">Mobile</div>
              <div class="detail-value">${booking.customerMobile}</div>
            </div>
          </div>
          
          <div class="tickets-section">
            <h3>Tickets</h3>
            ${booking?.bookingItems?.map((item: any) => `
              <div class="ticket-item">
                <div class="ticket-left">
                  <div class="ticket-name">${item?.ticketName}</div>
                  <div class="ticket-qty">Qty: ${item?.quantity || 0}</div>
                </div>
                <div class="ticket-price">₹${item?.appliedPrice}</div>
              </div>
            `).join("")}
            <div class="total">
              <span>Total Amount</span>
              <span>₹${booking.totalAmount}</span>
            </div>
          </div>
          
          <div class="footer">
            <p>This is your booking confirmation. Keep this ticket for entry.</p>
            <p>Contact: support@aerocity.com | +91-XXXX-XXXX</p>
          </div>
        </div>
      </body>
      </html>
    `;

    printWindow.document.write(ticketHTML);
    printWindow.document.close();
    setTimeout(() => {
      printWindow.focus();
      printWindow.print();
    }, 250);
  };

  const handleDownloadPDF = () => {
    // Open a new window with the ticket
    const printWindow = window.open("", "PRINT", "height=800,width=1000");
    if (!printWindow) {
      alert("Please allow pop-ups to download the ticket");
      return;
    }

    const ticketHTML = `
      <!DOCTYPE html>
      <html>
      <head>
        <meta charset="UTF-8">
        <title>Aerocity Booking - ${booking.bookingReference}</title>
        <style>
          * { margin: 0; padding: 0; box-sizing: border-box; }
          body { 
            font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', sans-serif; 
            padding: 20px; 
            background: #f5f5f5;
          }
          .ticket { 
            max-width: 700px; 
            margin: 20px auto; 
            border: 3px solid #0284c7; 
            border-radius: 12px; 
            padding: 40px; 
            background: white;
            box-shadow: 0 10px 30px rgba(0,0,0,0.1);
          }
          .header { text-align: center; margin-bottom: 30px; }
          .header h1 { font-size: 32px; color: #333; margin-bottom: 5px; font-weight: bold; }
          .header p { color: #666; font-size: 14px; }
          .reference { 
            background: linear-gradient(135deg, #0284c7 0%, #0369a1 100%); 
            border-radius: 8px; 
            padding: 25px; 
            text-align: center; 
            margin: 25px 0;
            color: white;
          }
          .reference-label { font-size: 11px; font-weight: bold; opacity: 0.9; }
          .reference-code { 
            font-size: 32px; 
            font-weight: bold; 
            font-family: 'Courier New', monospace; 
            letter-spacing: 3px;
            margin-top: 10px;
          }
          .qr-section { text-align: center; margin: 35px 0; }
          .qr-section img { max-width: 200px; height: auto; border: 2px solid #ddd; border-radius: 8px; }
          .qr-section p { margin-top: 12px; font-size: 12px; color: #666; }
          .details { 
            display: grid; 
            grid-template-columns: 1fr 1fr; 
            gap: 25px; 
            margin: 30px 0;
            padding: 20px 0;
            border-top: 1px solid #eee;
            border-bottom: 1px solid #eee;
          }
          .detail-group { }
          .detail-label { 
            font-size: 11px; 
            color: #666; 
            font-weight: 700; 
            text-transform: uppercase; 
            letter-spacing: 0.5px;
          }
          .detail-value { 
            font-size: 16px; 
            color: #333; 
            margin-top: 6px;
            font-weight: 500;
          }
          .tickets-section { margin: 30px 0; }
          .tickets-section h3 { 
            font-size: 16px; 
            font-weight: bold; 
            margin-bottom: 15px;
            color: #333;
          }
          .ticket-item { 
            display: flex; 
            justify-content: space-between; 
            align-items: center;
            padding: 12px 0; 
            font-size: 14px;
            border-bottom: 1px solid #f0f0f0;
          }
          .ticket-left { flex: 1; }
          .ticket-name { color: #333; font-weight: 500; }
          .ticket-qty { color: #999; font-size: 13px; }
          .ticket-price { color: #666; font-weight: 500; min-width: 80px; text-align: right; }
          .total { 
            display: flex; 
            justify-content: space-between;
            align-items: center;
            border-top: 2px solid #333; 
            padding-top: 15px; 
            margin-top: 15px; 
            font-weight: bold; 
            font-size: 20px;
            color: #0284c7;
          }
          .footer { 
            text-align: center; 
            margin-top: 40px; 
            font-size: 11px; 
            color: #999; 
            padding-top: 20px; 
            border-top: 1px solid #eee;
          }
          .print-button {
            text-align: center;
            margin-top: 30px;
          }
          .print-button button {
            background: #0284c7;
            color: white;
            border: none;
            padding: 12px 30px;
            font-size: 16px;
            border-radius: 6px;
            cursor: pointer;
            font-weight: 600;
          }
          .print-button button:hover {
            background: #0369a1;
          }
          @media print { 
            body { padding: 0; margin: 0; background: white; }
            .ticket { 
              margin: 0; 
              box-shadow: none;
              page-break-inside: avoid;
            }
            .print-button { display: none; }
          }
        </style>
      </head>
      <body>
        <div class="ticket">
          <div class="header">
            <h1>AEROCITY</h1>
            <p>Water Park Booking Ticket</p>
          </div>
          
          <div class="reference">
            <div class="reference-label">Booking Reference</div>
            <div class="reference-code">${booking.bookingReference}</div>
          </div>
          
          <div class="qr-section">
            <img src="${qrCodeUrl}" alt="Booking QR Code" />
            <p>Scan this code at gate for entry</p>
          </div>
          
          <div class="details">
            <div class="detail-group">
              <div class="detail-label">Visit Date</div>
              <div class="detail-value">${new Date(booking.visitDate).toLocaleDateString("en-IN", { weekday: "short", month: "short", day: "numeric", year: "numeric" })}</div>
            </div>
            <div class="detail-group">
              <div class="detail-label">Status</div>
              <div class="detail-value" style="color: green;">✓ Confirmed</div>
            </div>
            <div class="detail-group">
              <div class="detail-label">Customer Name</div>
              <div class="detail-value">${booking.customerName}</div>
            </div>
            <div class="detail-group">
              <div class="detail-label">Mobile</div>
              <div class="detail-value">${booking.customerMobile}</div>
            </div>
          </div>
          
          <div class="tickets-section">
            <h3>Tickets</h3>
            ${booking?.bookingItems?.map((item: any) => `
              <div class="ticket-item">
                <div class="ticket-left">
                  <div class="ticket-name">${item.ticketName}</div>
                  <div class="ticket-qty">Qty: ${item.quantity}</div>
                </div>
                <div class="ticket-price">₹${item.appliedPrice}</div>
              </div>
            `).join("")}
            <div class="total">
              <span>Total Amount</span>
              <span>₹${booking.totalAmount}</span>
            </div>
          </div>
          
          <div class="footer">
            <p>This is your booking confirmation. Keep this ticket for entry.</p>
            <p>Contact: support@aerocity.com | +91-XXXX-XXXX</p>
            <p>Downloaded: ${new Date().toLocaleString()}</p>
          </div>
        </div>
        
        <div class="print-button">
          <button onclick="window.print()">Print to PDF</button>
        </div>
        
        <script>
          document.onkeydown = function(e) {
            if (e.key === 'Escape') window.close();
          }
        </script>
      </body>
      </html>
    `;

    printWindow.document.write(ticketHTML);
    printWindow.document.close();
  };

  const visitDate = new Date(booking.visitDate);

  return (
    <section className="py-10 lg:py-16">
      <div className="mx-auto max-w-2xl px-4 lg:px-8">
        {/* Success Banner */}
        <div className="mb-8 flex flex-col items-center gap-4 text-center">
          <div className="flex h-16 w-16 items-center justify-center rounded-full bg-secondary/10">
            <CheckCircle className="h-10 w-10 text-secondary" />
          </div>
          <h1 className="text-2xl font-bold text-foreground sm:text-3xl">
            Booking Confirmed!
          </h1>
          <p className="text-muted-foreground">
            Your tickets have been booked successfully. A confirmation SMS has
            been sent to your mobile.
          </p>
        </div>

        {/* Booking Reference */}
        <div className="ticket-to-print">
          <div className="mb-6 rounded-xl border-2 border-secondary bg-secondary/5 p-6 text-center">
            <p className="text-xs text-muted-foreground">Booking Reference</p>
            <p className="mt-1 text-2xl font-bold tracking-wider text-primary">
              {booking.bookingReference}
            </p>
          </div>

          {/* QR Code */}
          <div className="mb-6 flex flex-col items-center gap-3 rounded-xl border bg-card p-6">
            {qrCodeUrl ? (
              <>
                <img src={qrCodeUrl} alt="Booking QR Code" className="h-40 w-40 rounded-lg" />
                <p className="text-xs text-muted-foreground">
                  Show this QR code at the park entrance for quick entry
                </p>
              </>
            ) : (
              <div className="flex h-40 w-40 items-center justify-center rounded-xl bg-muted">
                <QrCode className="h-20 w-20 text-muted-foreground/40" />
              </div>
            )}
          </div>

          {/* Booking Details */}
          <div className="flex flex-col gap-4">
          {/* Visit Date */}
          <div className="rounded-xl border bg-card p-5">
            <div className="flex items-center gap-3">
              <CalendarDays className="h-5 w-5 text-secondary" />
              <div>
                <p className="text-xs text-muted-foreground">Visit Date</p>
                <p className="font-semibold text-card-foreground">
                  {formatDate(visitDate)}
                </p>
              </div>
            </div>
          </div>

          {/* Tickets */}
          <div className="rounded-xl border bg-card p-5">
            <h3 className="mb-3 text-sm font-semibold uppercase tracking-wider text-muted-foreground">
              Tickets
            </h3>
            <div className="flex flex-col gap-2">
              {booking?.bookingItems?.map((item: any) => (
                <div
                  key={item.id}
                  className="flex items-center justify-between text-sm"
                >
                  <span className="text-card-foreground">
                    {item.ticketName}{" "}
                    <span className="text-muted-foreground">x{item.quantity}</span>
                  </span>
                  <span className="font-medium text-card-foreground">
                    {formatPrice(item.totalPrice)}
                  </span>
                </div>
              ))}
              {booking.offer && (
                <div className="mt-2 flex items-center gap-2 rounded-lg bg-accent/10 px-3 py-2 text-xs text-accent">
                  <Tag className="h-3.5 w-3.5" />
                  {booking.offer.name} applied
                </div>
              )}
              <div className="mt-2 flex items-center justify-between border-t pt-3">
                <span className="text-base font-semibold text-card-foreground">
                  Total Paid
                </span>
                <span className="text-xl font-bold text-primary">
                  {formatPrice(booking.totalAmount)}
                </span>
              </div>
            </div>
          </div>

          {/* Customer Details */}
          <div className="rounded-xl border bg-card p-5">
            <h3 className="mb-3 text-sm font-semibold uppercase tracking-wider text-muted-foreground">
              Booked By
            </h3>
            <div className="flex flex-col gap-2">
              <div className="flex items-center gap-3 text-sm">
                <User className="h-4 w-4 text-muted-foreground" />
                <span className="text-card-foreground">
                  {booking.customerName}
                </span>
              </div>
              <div className="flex items-center gap-3 text-sm">
                <Phone className="h-4 w-4 text-muted-foreground" />
                <span className="text-card-foreground">
                  +91 {booking.customerMobile}
                </span>
              </div>
              {booking.customerEmail && (
                <div className="flex items-center gap-3 text-sm">
                  <Mail className="h-4 w-4 text-muted-foreground" />
                  <span className="text-card-foreground">
                    {booking.customerEmail}
                  </span>
                </div>
              )}
            </div>
          </div>
        </div>
        </div>

        {/* Actions */}
        <div className="no-print mt-8 flex flex-col gap-3 sm:flex-row">
          <button
            onClick={handlePrint}
            className="inline-flex flex-1 items-center justify-center gap-2 rounded-lg border px-6 py-3 text-sm font-medium text-foreground transition-all hover:bg-muted"
          >
            <Printer className="h-4 w-4" />
            Print Ticket
          </button>
          <button
            onClick={handleDownloadPDF}
            className="inline-flex flex-1 items-center justify-center gap-2 rounded-lg border px-6 py-3 text-sm font-medium text-foreground transition-all hover:bg-muted"
          >
            <Download className="h-4 w-4" />
            Download PDF
          </button>
        </div>

        <div className="no-print mt-6 text-center">
          <Link
            href="/"
            className="inline-flex items-center gap-2 text-sm font-medium text-primary hover:underline"
          >
            <Home className="h-4 w-4" />
            Back to Home
          </Link>
        </div>
      </div>
    </section>
  );
}

export default function ConfirmationPage() {
  return (
    <Suspense
      fallback={
        <div className="flex min-h-[60vh] items-center justify-center">
          <p className="text-muted-foreground">Loading confirmation...</p>
        </div>
      }
    >
      <ConfirmationContent />
    </Suspense>
  );
}
